 /* animation: addPadding 2s alternate; */
}

.ant-menu-inline .ant-menu-item-selected .ant-menu-title-content {
 animation: rotate 0.2s 1 normal forwards;
}

.ant-menu-inline-collapsed .ant-menu-item-selected {
