@keyframes addPadding {
 0% {
  padding: 20px 20px;
 }
 100% {
  padding: 30px 20px;
 }
}

.img-logo {
 transition: transform 1s;
 box-shadow: 0px 0px 20px #000;
 border-radius: 150px;
}

.img-logo:hover {
 transform: rotate(-20deg);
 /* transform: rotate(20deg); */
 /* animation: rotate 1s linear 0.5s 1 normal forwards; */
}

@keyframes rotate {
 0% {
  transform: scale(1, 1);
 }
 50% {
  transform: scale(2, 1.5);
 }
 /* 100% {transform: scale(1,1);} */
}

