	font-size: 1%;
	opacity: .6;
	position: absolute;
	width: 5px;
	height: 5px
}

.leaflet-edit-marker-selected {
	background-color: rgba(254, 87, 161, 0.1);
	border: 4px dashed rgba(254, 87, 161, 0.6);
	border-radius: 4px;
	box-sizing: content-box
}

.leaflet-edit-move {
	cursor: move
}

.leaflet-edit-resize {
